import React from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {loadStocks} from './store';
import './App.css'

const StockPagination = () => {
    const dispatch = useDispatch();
    const currentPage = useSelector(state => state.currentPage);
    const totalStocks = useSelector(state => state.totalStocks);
    const stocksPerPage = useSelector(state => state.stocksPerPage);

    const totalPages = Math.ceil(totalStocks / stocksPerPage);

    const handleNextClick = () => {
        dispatch(loadStocks(currentPage + 1, stocksPerPage));
    };

    const handlePrevClick = () => {
        dispatch(loadStocks(currentPage - 1, stocksPerPage));
    };

    return (
        <div className="button-container">
            <button disabled={currentPage === 1} onClick={handlePrevClick}>Prev</button>
            <button disabled={currentPage === totalPages} onClick={handleNextClick}>Next</button>
        </div>
    );
};

export default StockPagination;