import {legacy_createStore as createStore, applyMiddleware} from 'redux';
import thunkMiddleware from 'redux-thunk';
import axios from 'axios';

const initialState = {
    currentPage: 1,
    stocksPerPage: 10,
    totalStocks: 0,
    stocks: []
};

// Actions
export const loadStocks = (page, limit) => {

    return async (dispatch) => {
        try {
            const token = 'INPUT_TOKEN';    //INPUT YOUR TOKEN
            const res = await axios.get(`https://cloud.iexapis.com/stable/stock/market/list/mostactive?token=${token}&range=1m&last=${limit * page}`);
            dispatch({type: 'LOAD_STOCKS_SUCCESS', payload: {data: res.data, page, limit}});
        } catch (error) {
            console.log(error);
        }
    }
}

// Reducer
export const stocksReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'LOAD_STOCKS_SUCCESS':
            return {
                ...state,
                currentPage: action.payload.page,
                stocksPerPage: action.payload.limit,
                totalStocks: action.payload.data.length,
                stocks: action.payload.data
            };
        default:
            return state;
    }
}

// Store
export const store = createStore(stocksReducer, applyMiddleware(thunkMiddleware));