import React, {useState} from 'react';
import {useSelector} from 'react-redux';
import './App.css'
import {DragDropContext, Droppable, Draggable} from 'react-beautiful-dnd';

const StockTable = () => {
    const stocks = useSelector(state => state.stocks);
    const currentPage = useSelector(state => state.currentPage);
    const stocksPerPage = useSelector(state => state.stocksPerPage);

    const startIndex = (currentPage - 1) * stocksPerPage;
    const endIndex = startIndex + stocksPerPage;
    const [tableData, setTableData] = useState(stocks);

    function handleDragEnd(result) {

        if (!result.destination) {
            return;
        }

        const {source, destination} = result;

        const reorderedTableData = [...tableData];
        const [removed] = reorderedTableData.splice(source.index, 1);
        reorderedTableData.splice(destination.index, 0, removed);

        setTableData(reorderedTableData);
    }

    return (
        <DragDropContext onDragEnd={handleDragEnd}>
            <table>
                <thead>
                <tr>
                    <th key="symbol">Symbol</th>
                    <th key="company-name">Company Name</th>
                    <th key="latest-price">Latest Price</th>
                    <th key="change">Change</th>
                    <th key="change-percent">Change %</th>
                </tr>
                </thead>
                <Droppable droppableId="stocks">
                    {(provided) => (
                        <tbody {...provided.droppableProps} ref={provided.innerRef}>
                        {stocks.slice(startIndex, endIndex).map((stock, index) => (
                            <Draggable key={stock.symbol} draggableId={stock.symbol} index={index}>
                                {(provided) => (
                                    <tr
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                        ref={provided.innerRef}
                                    >
                                        <td className={stock.symbol}>{stock.symbol}</td>
                                        <td className={stock.symbol}>{stock.companyName}</td>
                                        <td className={stock.symbol}>{stock.latestPrice}</td>
                                        <td className={stock.symbol}>{stock.change}</td>
                                        <td className={stock.symbol}>{(stock.changePercent * 100).toFixed(2)}%</td>
                                    </tr>
                                )}
                            </Draggable>
                        ))}
                        {provided.placeholder}
                        </tbody>
                    )}
                </Droppable>
            </table>
        </DragDropContext>
    );
};

export default StockTable;